.testClass {
  color: blue;
  font-size: 2em;
  font-weight: 600; }

.container {
  padding: 10%; }

.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white; }
  .App-header .yellow {
    color: #ffed00; }
  .App-header .blue {
    color: #00c9ff; }
  .App-header .green {
    color: #64ff00; }

.App-title {
  font-size: 1.5em; }

.App-intro {
  font-size: large; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

.display {
  display: block; }

.hidden {
  display: none; }



